
@import url('https://fonts.googleapis.com/css?family=Raleway:400,700');
v-cloak{
  display: none;
}
body{
  width: 100%;
  background: rgb(230,230,230);
  font-family: 'Raleway', sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
}
h1, h2, h3, h4, h5{
  padding: 0;
  margin: 0;
}
ul, li, ol{
  margin: 0;
  padding: 0;
  list-style: none;
}
button{
  background: none;
  border: none;
}
p{
  margin: 0;
  padding: 5px 0;
}
a{
  text-decoration: none;
}
.a11y-hidden{
  position: absolute;
  clip: rect(0,0,0,0);
  width: 1px;
  height: 1px;
  margin: -1000px;
  overflow: hidden;
}



